import { useFormik } from 'formik'
import { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import * as Yup from 'yup'
import { RootReducer } from '../../store'
import { close, remove } from '../../store/reducers/cart'
import {
  CartButton,
  CartContainer,
  CartItem,
  Overlay,
  PriceContainer,
  Sidebar,
} from './styles'

export const formataPreco = (preco = 0) => {
  return new Intl.NumberFormat('pt-BR', {
    style: 'currency',
    currency: 'BRL',
  }).format(preco)
}

const Cart = () => {
  const dispatch = useDispatch()
  const { isOpen, items } = useSelector((state: RootReducer) => state.cart)
  const [isCheckout, setIsCheckout] = useState(false)

  const closeCart = () => {
    dispatch(close())
  }

  const getTotalPrice = () => {
    return items.reduce((acumulador, valorAtual) => {
      return (acumulador += valorAtual.preco)
    }, 0)
  }

  const removeItem = (id: number) => {
    dispatch(remove(id))
  }

  const form = useFormik({
    initialValues: {
      fullName: '',
      address: '',
      city: '',
      cep: '',
      houseNumber: '',
      houseComplement: '',
    },
    validationSchema: Yup.object({
      fullName: Yup.string()
        .min(5, 'O nome precisa ter pelo menos 5 caracteres')
        .required('O campo  obrigatrio'),
      address: Yup.string()
        .min(5, 'O endereo precisa ter pelo menos 5 caracteres')
        .required('O campo  obrigatrio'),
      city: Yup.string()
        .min(3, 'A cidade precisa ter pelo menos 3 caracteres')
        .required('O campo  obrigatrio'),
      cep: Yup.string()
        .length(8, 'O CEP precisa ter 8 caracteres')
        .required('O campo  obrigatrio'),
      houseNumber: Yup.number()
        .typeError('O nmero deve ser vlido')
        .min(1, 'O nmero precisa ser maior que 0')
        .required('O campo  obrigatrio'),
      houseComplement: Yup.string().nullable(),
    }),
    onSubmit: (values) => {
      console.log('Dados do formulrio:', values)
    },
  })

  const getErrorMessage = (fieldName: keyof typeof form.values) => {
    if (form.touched[fieldName] && form.errors[fieldName]) {
      return form.errors[fieldName]
    }
    return null
  }

  return (
    <CartContainer className={isOpen ? 'is-open' : ''}>
      <Overlay onClick={closeCart} />
      <Sidebar>
        {isCheckout ? (
          <>
            <h2>Entrega</h2>
            <form onSubmit={form.handleSubmit}>
              <div>
                <label htmlFor="fullName">Quem ir receber</label>
                <input
                  id="fullName"
                  type="text"
                  name="fullName"
                  value={form.values.fullName}
                  onChange={form.handleChange}
                  onBlur={form.handleBlur}
                />
                <small>{getErrorMessage('fullName')}</small>
              </div>
              {/* Adicione os demais campos conforme necessrio */}
              <CartButton type="submit">Finalizar compra</CartButton>
              <CartButton type="button" onClick={() => setIsCheckout(false)}>
                Voltar para o carrinho
              </CartButton>
            </form>
          </>
        ) : (
          <>
            <ul>
              {items.map((item) => (
                <CartItem key={item.id}>
                  <img src={item.foto} alt={item.nome} />
                  <div>
                    <h3>{item.nome}</h3>
                    <span>{formataPreco(item.preco)} </span>
                  </div>
                  <button onClick={() => removeItem(item.id)} type="button" />
                </CartItem>
              ))}
            </ul>
            <PriceContainer>
              <p>Valor total</p>
              <p>{formataPreco(getTotalPrice())}</p>
            </PriceContainer>
            <CartButton onClick={() => setIsCheckout(true)}>
              Continuar com a entrega
            </CartButton>
          </>
        )}
      </Sidebar>
    </CartContainer>
  )
}

export default Cart
